header {
  background-image: radial-gradient(#0008 0%, #0006 50%, #0000 100%), url("food.c29f65fc.jpg");
  background-position: center;
  background-size: cover;
  justify-content: center;
  align-items: center;
  height: 61dvh;
  display: flex;
}

.logoLink {
  padding: 16px;
  display: block;
  scale: .5;
}

nav {
  z-index: 1000;
  position: fixed;
  bottom: 0;
}

.nav {
  background: #fff;
  justify-content: center;
  align-items: center;
  width: 100dvw;
  padding: 4px 0;
  display: flex;
}

.listWrapper {
  border-radius: 16px;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  max-width: 1578px;
  display: flex;
}

.navigation {
  justify-content: space-between;
  width: 100%;
  list-style: none;
  display: flex;
}

.navigation li {
  flex: 1;
}

.navigation li a {
  text-align: center;
  color: #420;
  text-shadow: 0 1px 8px #fff;
  border-radius: 16px;
  justify-content: center;
  align-items: center;
  height: 48px;
  margin: 0 4px;
  padding: 0 4px;
  font-size: 14px;
  line-height: 20px;
  text-decoration: none;
  transition: scale .12s ease-in-out, background-color .12s ease-in-out;
  display: flex;
}

.navigation li a.active, .navigation li a:hover {
  background-color: #ffd780;
  scale: 1.05;
}

.navigation li a:hover:not(:active) {
  background-color: #ffd780bb;
  scale: 1.08;
}

@media screen and (width >= 768px) {
  .logoLink {
    border-radius: 64px;
    transition: scale .24s ease-in-out;
    scale: 1;
  }

  .logoLink:hover {
    scale: 1.1;
  }

  .logoLink:hover:not(:active) {
    scale: 1.12;
  }

  nav {
    position: sticky;
    top: 0;
  }

  .nav {
    border-top: 3px solid #fff;
    border-bottom: 3px solid #fff;
  }

  .navigation li a {
    height: 56px;
    margin: 0 8px;
    padding: 0 16px;
    font-size: 28px;
    line-height: 32px;
  }

  .listWrapper {
    margin: 0 16px;
  }
}

